* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    color: #333;
    font-size: 62.5%;
    font-family: "Open Sans", sans-serif;
}

.mainLogin {
    background-image: url("./assets/img/login.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    min-height: 100vh;
    display: flex;
    justify-content: center;
}

.form {
    opacity: 0.9;
    padding: 32px 24px;
    width: 360px;
    min-height: 100px;
    background: #fff;
    text-align: center;
    border-radius: 2px;
    margin: 24px;
    align-self: center;
    box-shadow: 0 2px 5px 0 rgba(51, 62, 73, 0.1);
}

.form .heading {
    font-size: 2rem;
}

.form .desc {
    font-size: 1.6rem;
    color: #636d77;
    text-align: center;
    line-height: 2.4rem;
    margin-top: 16px;
    font-weight: 300;
    font-weight: lighter;
}

.spacer {
    margin-top: 36px;
}

.form-group {
    display: flex;
    margin-bottom: 16px;
    flex-direction: column;
}

.form-label {
    text-align: left;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.8rem;
    padding-bottom: 6px;
}

.form-control {
    height: 40px;
    padding: 8px 12px;
    border: 1px solid #b1b1b1;
    border-radius: 5px;
    outline: none;
    font-size: 1.4rem;
}

.form-control:hover {
    border-color: #1dbfaf;
}

.form-group.invalid .form-control {
    border-color: #f33a58;
}

.form-group.invalid .form-message {
    color: #f33a58;
}

.form-message {
    text-align: left;
    font-size: 1.2rem;
    line-height: 1.6rem;
    padding: 4px 0 0;
}

.form-submit {
    outline: none;
    background-color: #1dbfaf;
    margin-top: 12px;
    padding: 12px 16px;
    font-weight: 600;
    border: none;
    color: #fff;
    font-size: 1.4rem;
    width: 100%;
    border-radius: 8px;
    cursor: pointer;
    width: 100%;
    display: block;
}

.tabs {
    display: flex;
}

.tab {
    outline: none;
    background-color: #1dbfaf;
    margin-top: 12px;
    padding: 12px 16px;
    font-weight: 600;
    border: none;
    color: #fff;
    font-size: 1.4rem;
    width: 49%;
    cursor: pointer;
    text-decoration: none;
}

.hide {
    background-color: #ccc;
    color: #333;
}

.form-submit:hover {
    background-color: #1ac7b6;
}

.radioBox {
    display: flex;
}

.radioBox .radioBox-child {
    display: flex;
    align-items: center;
    font-size: 1.4rem;
    margin-right: 30px;
}

.radioBox .form-control {
    margin-right: 10px;
}